<template>
  <div class="tip">
    <ul :class="[{'move1': count === 2, 'move2': count === 3}]">
      <li v-for="(item, idx) in messageList" :key="idx" v-if="item.show" :class="item.name ? 'fault' : 'time'" @click="toDetail(idx)">
        {{item.word}}
        <span class="right-arrow" v-if="item.jump"></span>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'tip',
  props: ['messageList'],
  data () {
    return {

    }
  },
  methods: {
    toDetail (index) {
      this.$emit('toDetail', index)
    }
  },
  computed: {
    count () {
      let num = 0
      for (let i = 0; i < this.messageList.length; i++) {
        if (this.messageList[i].show === 1) {
          num++
        }
      }
      return num
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tip{
  width: 100%;
  height:3.2rem;
  position: absolute;
  left:0;
  top:68px;
  z-index: 10;
  overflow: hidden;
  background-color: rgba(255,255,255,.15);
}
ul{
  width: 100%;
  height:3.2rem;
}
li{
  height: 3.2rem;
  line-height: 3.2rem;
  padding-left:3.8rem;
  background-repeat: no-repeat;
  -webkit-background-size: 2.4rem 2.4rem;
  background-size: 2.4rem 2.4rem;
  background-position: 1rem center;
  z-index: 50;
  font-size: 1.5rem;
}
.right-arrow{
  position: absolute;
  right: 1.5rem;
  height: 3.2rem;
  width: 1.6rem;
  background-size: 1.2rem 2.4rem;
  background-image: url(../../static/img/icon_arrow.png);
  background-position: center;
  background-repeat: no-repeat;
}
.fault{
  /*background-color: rgba(255,255,255,.5);*/
  background-image: url(../../static/img/icon_worning.png);
  color: #ffffff;
}
.time{
  /*background-color: rgba(255,255,255,.5);*/
  background-image: url(../../static/img/icon_prompt.png);
  color: #f58b23;
}
@keyframes move1{
  0%{transform: translateY(3.2rem);}
  50%{transform: translateY(0rem);}
  100%{transform: translateY(-3.2rem);}
}
.move1{
  animation: move1 2s cubic-bezier(0,1,0,1) infinite;
}
@keyframes move2{
  0%{transform: translateY(3.2rem);}
  33.3%{transform: translateY(0rem);}
  66.6%{transform: translateY(-3.2rem);}
  100%{transform: translateY(-6.4rem);}
}
.move2{
  animation: move2 3s cubic-bezier(0,1,0,1) infinite;
}
</style>



