<template>
  <div class='selectlist'>
    <div class="selectFixedLayer" @click.stop.prevent="chooseIndex('return')"  v-if="isListShow"></div>
    <transition name="choose-box"  v-if="isListShow">
      <div class="selectContainerBox" :style="{'marginLeft':offset}">
        <ul class="selectContainer">
          <li v-for="(item,index) in selectList" :key="index" @click.stop.prevent="chooseIndex(index)">
            {{item}}
          </li>
        </ul>
      </div>
    </transition>
  </div>
</template>
<script>
/**
 * 底部按钮弹出选择框组件
 * 从父组件传值isListShow、selectList
 * isListShow:当前选择框是否显示(bool)
 * selectList:选择框的内容列表(Array)
 * chooseIndex(index): 子组件向父组件传值(index)，index为选中的列表序列号，当index==="return"时，表示点击的是选择框后面的模态框
 */
export default {
  name: 'selectlist',
  props: ['isListShow', 'selectList', 'offset'],
  data () {
    return {
    }
  },
  mounted () {
  },
  methods: {
    chooseIndex (index) {
      this.$emit('chooseIndex', index)
    }
  }
}
</script>
<style lang="css" scoped>
.selectFixedLayer{
  position:fixed;
  width: 100%;
  height: 100%;
  top:0;
  left:0;
  z-index:499;
}
.selectContainerBox{
  padding: 0 .6rem 0 1.6rem;
  max-height: 30rem;
  width: 9rem;
  position: absolute;
  z-index: 99999;
  left: 50%;
  bottom: calc( 100% + 10px);
  transform: translate(-50%,0);
  background-color: #fff;
  border-radius: .8rem;
  box-shadow:0px 2px 4px rgba(0,0,0,.15);
}
.selectContainer {
  margin:.8rem 0;
  max-height: 28.4rem;
  /*position: absolute;*/
  top: 0;
  left: 0;
  overflow-y: scroll;
  z-index: 500;
  box-sizing: border-box;
  /* transition: all 1s; */
}
.selectContainer::-webkit-scrollbar{
  width: .4rem;
  /*background-color: #ccc;*/
  /*color: red;*/
  position: absolute;
  left: .1rem;
}
.selectContainer::-webkit-scrollbar-thumb{
  background-color: rgba(0,0,0,.15);
  border-radius: 2px;
}
.selectContainer li {
  font-size: 1.5rem;
  /*letter-spacing: 1px;*/
  height:4.8rem;
  line-height: 4.8rem;
  margin-right: .6rem;
  color: rgba(0,0,0,1);
  border-bottom: 1px solid rgba(0,0,0,.05);
  box-sizing: border-box;
  text-align: left
}
.selectContainer li:first-child{
  height: 4.2rem;
  line-height: 3.4rem;
}
.selectContainer li:last-child {
  height: 4.2rem;
  border: none;
}

.choose-box-enter-active {
  transition: all .5s ease;
}
.choose-box-leave-active {
  transition: all .3s ease;
}
.choose-box-enter, .choose-box-leave-to {
  transform: translate(-50%,30%);
  opacity: 0;
}
</style>



