<template>
  <div id='midControl'>
    <div class="control"  @touchstart.stop="startPostion" @touchend.stop="endPostion">
      <div class="controlBox"  :style="{transform:'translateX(-'+currentPage*100+'%)'}">
        <div :class="['tempControl', {'border-1px-right':index<computedList.length-1&&(index+1)%4 != 0?true:false}]" v-for="(item,index) in computedList" :key="index" @click.stop.prevent="sendIndex(index,item.title)" :style="{'opacity':(item.forbidTouch|| controlList.forbidAll)?.6:1}">
          <h3>{{item.title}}<b v-if="controlList.wordPlace">{{"("+item.word+")"}}</b></h3>
          <div>
            <span v-for="(im,ix) in item.number" :key='ix'>{{im}}<b v-if="!controlList.wordPlace">{{item.word[ix]}}</b></span>
            <i :class="[{arrow:!item.noArrow}]"></i>
          </div>
        </div>
      </div>
      <ul class="pageList" v-if="totalPage">
        <li v-for="(i, index) in (totalPage+1)" :key="index" :class="[{'focus':i===currentPage+1}]"></li>
      </ul>
    </div>
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: 'mid-control',
  props: ['controlList'],
  data () {
    return {
      startX: 0,
      endX: 0,
      currentPage: 0,
      totalPage: 0
    }
  },
  mounted () {
    this.totalPage = Math.ceil(this.controlList.data.length / 4) - 1
  },
  computed: {
    computedList () {
      let arr = []
      this.controlList.data.forEach((item) => {
        if (typeof item.number !== 'object') {
          item.number = [item.number]
          item.word = [item.word]
          arr.push(item)
          return
        }
        arr.push(item)
      })
      return arr
    }
  },
  methods: {
    sendIndex (index, b) {
      if (this.controlList.forbidAll || this.computedList[index].noArrow || this.computedList[index].forbidTouch) return
      this.$emit('getIndex', index, b)
    },
    startPostion (e) {
      this.startX = e.changedTouches[0].clientX
    },
    endPostion (e) {
      this.endX = e.changedTouches[0].clientX
      this.swiper()
    },
    swiper (flag) {
      if (Math.abs(this.startX - this.endX) < 30 && flag) {
        if (!this.bottomList.data[this.ind].forbid) {
          this.clickEvent(this.ind)
        }
      }
      if (this.startX - this.endX > 30 && this.currentPage < this.totalPage) {
        this.currentPage += 1
      } else if (this.endX - this.startX > 30 && this.currentPage > 0) {
        this.currentPage -= 1
      }
    }
  }
}
</script>
<style lang="css" scoped>
#midControl{
  width: 100%;
  padding: 2.4rem 1.5rem;
  align-items: center;
  color: #FFF;
  position: absolute;
  left: 0;
  bottom: 0;
  box-sizing: border-box;
}
.control{
  overflow: hidden;
}
.controlBox{
  /*margin:  2.4rem 0.8rem;*/
  /*background-color: skyblue;*/
  display: flex;
  align-items: center;
  color: #FFF;
  transition: all .5s;
  box-sizing: border-box;
}
.controlBox > div{
  flex: 1;
  min-width: 25%;
  height: 4.2rem;
  text-align: center;
  padding: 0 0.4rem;
  box-sizing: border-box;
  /*border-right: 0.05rem solid #f8cd81;*/
  /*border-right: 0.05rem solid rgba(255,255,255,.5);*/
}

.controlBox > div>h3{
  font-weight: normal;
  opacity: 0.5;
  font-size: 1rem;
  line-height: 1.3;
}
.controlBox > div > div{
  text-align: center;
  margin-top: 0.5rem;
}
 span{
  position: relative;
  font-size: 2.2rem;
  line-height: 2.2rem;
}
 i{
  display: inline-block;
  margin-left: 0.2rem;
}
#midControl b{
  font-weight: normal;
  font-size: 1.3rem;
}
.tempControl{
  position: relative;
}
.forbidTouch{
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 2;
}

.arrow{
    height: 10px;
    width: 10px;
    display: inline-block;
    transform: translateY(0.1rem);
    overflow: hidden;
    margin-right: -1.2rem;
    position: relative;
  }
.arrow::before{
  background-color: white;    /*三角形颜色*/
  content: "";
  width: 8px;
  height: 8px;
  display: block;
  transform:rotate(45deg);
  -ms-transform:rotate(45deg);   /* IE 9 */
  -moz-transform:rotate(45deg);  /* Firefox */
  -webkit-transform:rotate(45deg); /* Safari 和 Chrome */
  -o-transform:rotate(45deg);
  position: absolute;
  top: -6px;
  left: 1px;
}
.pageList {
  position: absolute;
  left: 50%;
  bottom:0.4rem;
  transform:translateX(-50%);
}
.pageList li {
  width: .5rem;
  height: .5rem;
  display: inline-block;
  vertical-align: middle;
  transform: scale(1);
  margin:0.2rem;
  border-radius: 50%;
  background-color: rgba(255,255,255,0.5);
}
.pageList li.focus {
  /*width: .6rem;
  height: .6rem;*/
  transform: scale(1.2);
  background-color: rgba(255,255,255,1);
}
.slots {
  overflow: visible;
  transform: translateX(-50%);
  position: absolute;
}
</style>



