<template>
  <div id='bottomMenu'>
    <ul class="menuList">
      <li v-for="(item,index) in bottomList.data" :key="index" :style="{backgroundImage:item.border?'url(./static/img/icon_lamp_mode_pre.png)':'url(./static/img/icon_lamp_normal.png)'}">
        <div :class="[{'button':bgActive&&index==ind}]" @touchstart.stop.prevent="changeBg(index)" @touchend.stop.prevent="clickEvent(index)" :style="{opacity:bottomList.switch && !bottomList.isSwitch && index || item.forbid? .3 : 1,backgroundImage:'url(' + (item.src.indexOf('/')>0?item.src:'./static/img/'+item.src )+')'}"></div>
        <p @touchend.stop.prevent="clickEvent(index)"  :class="[{'blueArrow':item.arrow}]" :style="{opacity:bottomList.switch && !bottomList.isSwitch && index || item.forbid? .3 : 1}">{{item.word}}</p>
        <slot :name="'slot'+index"></slot>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'bottom-menu',
  props: ['bottomList'],
  data () {
    return {
      bgActive: false,
      ind: null,
      borderColor: []
    }
  },
  created () {
    for (var i = 0; i < this.bottomList.data.length; i++) {
      this.borderColor[i] = 'rgba(0, 0, 0, .05)'
    }
  },
  methods: {
    changeBg (index) {
      if ((this.bottomList.switch && !this.bottomList.isSwitch && index) || this.bottomList.data[index].forbid) return
      this.bgActive = true
      this.ind = index
    },
    clickEvent (index) {
      this.bgActive = false
      this.ind = index
      if ((this.bottomList.switch && !this.bottomList.isSwitch && index) || this.bottomList.data[index].forbid) return
      this.$emit('buttonsClick', index)
    }
  }
}
</script>
<style lang="css" scoped>
#bottomMenu {
  position: absolute;
  bottom: 0;
  left:0;
  width: 100%;
  display:flex;
  flex-direction: column;
  justify-content: space-around;
  left: 0;
  bottom: 0;
}
#bottomMenu .menuList {
  margin: 2.4rem .8rem;
  display: flex;
  flex-direction: row;
  text-align: center;
}

#bottomMenu .menuList > li {
  flex: 1;
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  /*transition: all .5s;*/
  background-position: center top;
  -webkit-background-size: 7rem 7rem;
  background-size: 7rem 7rem;
  background-repeat: no-repeat;
}

#bottomMenu .menuList > li > div {
    background-repeat: no-repeat;
  -webkit-background-size: 2.4rem 2.4rem;
  background-size: 2.4rem 2.4rem;
  background-position: center center;
  border-radius: 50%;
  /*transition: all .3s;*/
}

#bottomMenu .menuList > li > div:first-child{
  width: 7rem;
  height: 7rem;
  margin-bottom: .6rem;
  box-sizing: border-box;
}

#bottomMenu .menuList > li > p {
  color: rgba(0,0,0,.7);
  font-size: 1.3rem;
  line-height:1.3;
  position: relative;
}

p.blueArrow::after {
    position: absolute;
    top: .4rem;
    right: -1rem;
    content: "";
    display:inline-block;
    vertical-align: middle;
    transform:translateY(15%);
    line-height:1.2rem;
    width: 0;
    height: 0;
    margin-left:.5rem;
    border: 3.5px solid transparent;
    border-top-color:rgb(0,125,255);
}

</style>



