<template>
  <div id="layout">
    <div :class="['content_box', type]" :style="[bgColorObj]">
      <div class="center_info" :style="[{'width': boxSize[0] + 'rem', 'height': boxSize[1] + 'rem'}]">
        <slot name="centerInfo"></slot>
      </div>
      <slot></slot>
    </div>
  </div>
</template>

<script>
/**
 * layout布局组件
 * Author: lizhuang lizhuang9@huawei.com 2017.11.07
 * props接收3个参数：
 * bgColor:   (String)可以接收渐变色('#4871cc,#50adfb')，或者单色('#8c97b7')
 * type:      (String)可选'center'(中间和底部居中显示)和'both'(顶部和底部间距均为2.4rem)
 * size:      (Array)[宽度,高度],均不带单位,缺省时为[32.4,24.4]
 */
export default {
  name: 'layout',
  props: ['bgColor', 'type', 'size'],
  components: {

  },
  computed: {
    bgColorObj () {
      if (this.bgColor.indexOf(',') > 0) {
        return {'backgroundImage': '-webkit-linear-gradient(top, ' + this.bgColor + ')'}
      } else {
        return {'background': this.bgColor}
      }
    },
    boxSize () {
      var width = this.size ? this.size[0] : 32.4
      var height = this.size ? this.size[1] : 24.4
      return [width, height]
    }
  },
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#layout .content_box{
  position: absolute;
  top: 0;
  bottom:13.6rem;
  width:100%;
  overflow: hidden;
}
#layout .content_box .center_info{
  margin:0 auto;
  text-align:center;
}
#layout .content_box.center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 68px
}

#layout .both .center_info {
  margin-top:2.4rem;
}
#layout .center_info div:first-child{
  width:100%;
  height:100%;
  background-size:contain;
  background-position: center center;
  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  transition: all .5s ease;
}
#layout .content_box .center_info .master_info{
  position: relative!important;
  margin-top: 0!important;
  bottom:auto!important;
}
#layout .content_box.both div:last-child{
  position: absolute;
  bottom: 2.4rem;
}
#layout .content_box.center div:last-child{
  position: relative;
  margin-top: .5rem;
}
</style>



