<template>
  <div class="container">
    <Tip :messageList="messageList" @toDetail="toErrorInfo" v-show="faultDetection.status"></Tip>
    <Layout :bgColor="bgColor" :type="'center'" :size="[36, 30.4]">
      <div slot="centerInfo" :class="[cleanerRobot.switch ? 'isOn' : 'isOff', 'masterInfo']">
        <div class="on" v-show="cleanerRobot.switch">
          <p class="wordDesc">剩余电量</p>
          <p class="battery" :style="{color: cleanerRobot.battery <= 20 ? '#FF3320' : '#007DFF'}">{{cleanerRobot.battery}}<span class="percent">%</span></p>
          <div class="modeLeft">
            <div class="modeItem wordDesc modeLeftItem">
              <p style="text-align: right;">静音模式</p>
              <p style="text-align: right;"><span class="itemNum">{{cleanerRobot.lefttime1}}</span>分钟</p>
            </div>
            <div class="modeItem wordDesc">
              <p>强效模式</p>
              <p><span class="itemNum">{{cleanerRobot.lefttime2}}</span>分钟</p>
            </div>
          </div>
        </div>
        <p class="off" v-show="!cleanerRobot.switch">
          已关机
        </p>
      </div>
      <Controls :controlList="controlList" :style="{opacity: cleanerRobot.switch ? '1' : '0.3'}"></Controls>
    </layout>
    <ButtomMenu :bottomList="bottomList" @buttonsClick="buttonsClick">
      <SelectList :slot="slot" :isListShow="isListShow" :selectList="selectList" :offset="offset" @chooseIndex="chooseModel"></SelectList>
    </ButtomMenu>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import Layout from './Layout'
import Tip from './Tip'
import Controls from './Controls'
import ButtomMenu from './BottomMenu'
import SelectList from './SelectList'
import faultDetection from '../common/faultDetection'
export default {
  name: 'sweeper',
  components: {
    Layout,
    Tip,
    Controls,
    ButtomMenu,
    SelectList
  },
  data () {
    return {
      slot: undefined,
      isListShow: 0,
      offset: 0,
      selectList: [],
      modelList: ['静音模式', '强效模式'],
      modeSrcList: ['icon_silent.png', 'icon_powerful.png']
    }
  },
  mounted () {

  },
  methods: {
    toErrorInfo (index) {
      if (this.messageList[index].jump) {
        this.$router.push({path: '/errorInfo/' + this.faultDetection.code})
      }
    },
    buttonsClick (index) {
      if (index > 0) {
        this.slot = 'slot' + index
      }
      switch (index) {
        case 0:
          this.$store.dispatch('setSwitchStatus')
          break
        case 1:
          this.isListShow = 1
          this.selectList = this.modelList
          break
        case 2:
          this.isListShow = 0
          this.$router.push({path: '/filter'})
          break
        case 3:
          this.isListShow = 0
          this.$router.push({path: '/statistics'})
          break
      }
    },
    chooseModel (index) {
      this.isListShow = 0
      if (index === 'return') {
        return
      }
      // 改变模式
      if (this.cleanerRobot.mode - 1 === index) return
      this.$store.dispatch('setMode')
      this.$store.dispatch('setSecondTitle')
    }
  },
  computed: {
    ...mapState(['cleanerRobot', 'faultDetection']),
    messageList () {
      return [
        {word: faultDetection[this.faultDetection.code], show: 1, name: 1, jump: 1}
        // {word: faultDetection[this.faultDetection.code], show: 1, name: 0, jump: 0}
      ]
    },
    errorCode () {
      return this.faultDetection.code
    },
    bgColor () {
      return this.cleanerRobot.switch ? '#4871cc, #50adfb' : '#8B96B6, #AFBACF'
    },
    bottomList () {
      let switchSrc = this.cleanerRobot.switch ? 'icon_newstyle_close_red.png' : 'icon_newstyle_close_blue.png'
      let switchWord = this.cleanerRobot.switch ? '关闭' : '开启'
      return {
        switch: 1,
        isSwitch: this.cleanerRobot.switch,
        data: [
          {
            src: switchSrc,
            word: switchWord,
            arrow: 0
          },
          {
            src: this.modeSrcList[this.cleanerRobot.mode - 1],
            word: this.modelList[this.cleanerRobot.mode - 1],
            arrow: 1,
            forbid: this.faultDetection.status
          },
          {
            src: 'icon_Filter_net.png',
            word: '滤网滤芯',
            arrow: 0,
            forbid: this.faultDetection.status
          },
          {
            src: 'icon_statistics.png',
            word: '统计',
            arrow: 0
          }
        ]
      }
    },
    controlList () {
      return {
        forbidAll: !this.cleanerRobot.switch,
        wordPlace: false,
        data: [
          {title: '本次清扫时间', number: this.cleanerRobot.time, word: '分钟', forbidTouch: 0, 'noArrow': 1},
          {title: '本次慢跑路程', number: this.cleanerRobot.distance, word: 'km', forbidTouch: 0, 'noArrow': 1},
          {title: '本次消耗卡路里', number: this.cleanerRobot.consume, word: 'kcal', forbidTouch: 0, 'noArrow': 1},
          {title: '累计清扫面积', number: this.cleanerRobot.totalarea, word: 'm²', forbidTouch: 0, 'noArrow': 1},
          {title: '累计清扫时间', number: this.cleanerRobot.totaltime, word: '分钟', forbidTouch: 0, 'noArrow': 1},
          {title: '累计慢跑路程', number: this.cleanerRobot.totaldistance, word: 'km', forbidTouch: 0, 'noArrow': 1},
          {title: '累计消耗卡路里', number: this.cleanerRobot.totalconsume, word: 'kcal', forbidTouch: 0, 'noArrow': 1}
        ]
      }
    }
  },
  watch: {
    errorCode () {

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .container{
    height: 100%;
    width: 100%;
  }
  .masterInfo, .on{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .isOn{
    background-image:url(../../static/img/img_dx_shang.png);
    background-repeat: no-repeat;
    position: relative;
  }
  .isOn::before{
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    background-image: url(../../static/img/img_dx_zhong.png), url(../../static/img/img_dx_di.png), url(../../static/img/img_bg_yuandian.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    animation: backgroundMove 20s linear infinite;
  }
  @keyframes backgroundMove{
    0%{transform: rotate(0deg);}
    100%{transform: rotate(360deg);}
  }
  .isOff{
    background: url(../../static/img/img_dx_shang.png) no-repeat;
  }
  .wordDesc{
    font-size: 1.3rem;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.4);
    margin: 0.4rem 0;
  }
  .off{
    font-size: 4.6rem;
    color: rgb(0, 0, 0);
    opacity: 0.2;
  }
  .battery{
    position: relative;
    font-size: 6.6rem;
  }
  .percent{
    font-size: 1.8rem;
  }
  .modeLeft{
    display: flex;
  }
  .modeItem{
    flex: 1;
    padding: 0 0.8rem;
  }
  .modeLeftItem{
    border-right: 1px solid rgba(0, 0, 0, 0.2);
  }
  .modeItem p{
    text-align: left;
    padding: 0.2rem 0;
  }
  .itemNum{
    font-size: 1.4rem;
  }
</style>



